body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  --time-duration: 60s;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch--speed-up {
  --time-duration: 10s;
}

.stopwatch__hand-minutes {
  transform-origin: bottom;
  animation: roll calc(var(--time-duration) * 60) steps(60) infinite;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  bottom: 40vmin;
  left: 38.5vmin;
  transform: translateX(50%);
}

.stopwatch__hand-seconds {
  transform-origin: bottom;
  animation: roll var(--time-duration) infinite linear;
  background-color: #2c8000;
  align-self: baseline;
  width: 1.5vmin;
  height: 38vmin;
  display: flex;
  position: absolute;
  bottom: 40vmin;
  left: 39.25vmin;
  transform: rotate(0);
}

.stopwatch__dot {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes roll {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
